import Vue from 'vue'
import Router from 'vue-router'
import Login from '../pages/Login' // 
import Home from '../pages/Home' // 
import CheckList from '../pages/CheckList' // 
import DateTimePicker from '../pages/DateTimePicker' // 
import UploadImg from '../pages/UploadImg' // 
import QR from '../pages/QR' // 
import Scan from '../pages/Scan' // 
import Scanner from '../pages/Scanner' // 
import LoadMore from '../pages/LoadMore' // /
import GetMore from '../pages/GetMore' // 
import Diagram from '../pages/Diagram' // 
import MessageBox from '../pages/MessageBox' // 
import Fold from '../pages/Fold' // 
import Toast from '../pages/Toast' // 
import Directive from '../pages/Directive' // 
import Filter from '../pages/Filter' // 
import MintUI from '../pages/MintUI' // mint-ui
import VuxUI from '../pages/VuxUI' // vux-ui webpack.base.conf.js
import WeexUI from '../pages/WeexUI' // weex-ui
import AudioBook from '../pages/AudioBook' // 
import Mine from '../pages/Mine' // 

Vue.use(Router)

export default new Router({
  routes: [
    // 
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    // 
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    // 
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    // 
    {
      path: '/checkList',
      name: 'CheckList',
      component: CheckList
    },
    // 
    {
      path: '/dateTimePicker',
      name: 'DateTimePicker',
      component: DateTimePicker
    },
    // 
    {
      path: '/uploadImg',
      name: 'UploadImg',
      component: UploadImg
    },
    // 
    {
      path: '/qr',
      name: 'QR',
      component: QR
    },
    // 
    {
      path: '/scan',
      name: 'Scan',
      component: Scan
    },
    // 
    {
      path: '/scanner',
      name: 'Scanner',
      component: Scanner
    },
    // /
    {
      path: '/loadMore',
      name: 'LoadMore',
      component: LoadMore
    },
    // 
    {
      path: '/getMore',
      name: 'GetMore',
      component: GetMore
    },
    // 
    {
      path: '/diagram',
      name: 'Diagram',
      component: Diagram
    },
    // 
    {
      path: '/messageBox',
      name: 'MessageBox',
      component: MessageBox
    },
    // 
    {
      path: '/fold',
      name: 'Fold',
      component: Fold
    },
    // 
    {
      path: '/toast',
      name: 'Toast',
      component: Toast
    },
    // 
    {
      path: '/directive',
      name: 'Directive',
      component: Directive
    },
    // 
    {
      path: '/filter',
      name: 'Filter',
      component: Filter
    },
    // mint-ui
    {
      path: '/mint-ui',
      name: 'MintUI',
      component: MintUI
    },
    // vux-ui
    {
      path: '/vux-ui',
      name: 'VuxUI',
      component: VuxUI
    },
    // weex-ui
    {
      path: '/weex-ui',
      name: 'WeexUI',
      component: WeexUI
    },
    // 
    {
      path: '/audioBook',
      name: 'AudioBook',
      component: AudioBook
    },
    // 
    {
      path: '/mine',
      name: 'Mine',
      component: Mine
    }
  ]
})



