/**
 * ()
 */
Vue.filter('rate', function (data) {
  if (data) {
    data = data.toString();
    console.log(data);
    var intS = data.substring(0, data.indexOf('.')) + data.substring(data.indexOf('.') + 1, data.indexOf('.') + 3);
    var floatS = data.substring(data.indexOf('.') + 3, data.indexOf('.') + 7);
    while (floatS.length < 4) {
      floatS = floatS + '0';
    }
    return parseInt(intS) + '.' + floatS + '%';
  }
  return '';
});

/**
 * 
 * fmtDate('yyyy,MM,dd')
 */
Vue.filter('fmtDate', (date, fmt) => {
    var o = {
      'M+': date.getMonth() + 1, // 
      'd+': date.getDate(), // 
      'h+': date.getHours(), // 
      'm+': date.getMinutes(), // 
      's+': date.getSeconds(), // 
      'q+': Math.floor((date.getMonth() + 3) / 3), // 
      'S': date.getMilliseconds() // 
    };
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
    }
    for (var k in o) {
      if (new RegExp('(' + k + ')').test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)));
      }
    }
    return fmt;
  }
);

/**
 * 
 * date:'yyyy-MM-dd'
 */
Vue.filter('fmtDateStr', (date) => {
  if(!date) return '';
  var temp = date.split(" ")[0].split("-");
  return temp.join("/");
});
Vue.filter('fmtDateStr2', (date) => {
  if(!date) return '';
  var temp = date.split(" ")[0].split("-");
  return temp[0] + "" + temp[1] + "" + temp[2] + "";
});
Vue.filter('fmtTimeStr', (date) => {
  if(!date) return '';
  var temp = date.split(" ")[0].split("-");
  return temp.join("/") + " " + date.split(" ")[1];
});
Vue.filter('fmtTimeStr2', (date) => {
  if(!date) return '';
  var temp = date.split(" ")[0].split("-");
  return temp[0] + "" + temp[1] + "" + temp[2] + "" + " " + date.split(" ")[1];
});

/**
 * 
 * new Date() --> 
 */
Vue.filter('fmtCurrentDate', (date) => {
  return date.getFullYear()+''+(date.getMonth()+1)+''+date.getDate()+'';
});

/**
 * 
 * new Date() --> :
 */
Vue.filter('fmtCurrentTime', (date) => {
  var minutes;
  if(date.getMinutes() < 10){
    minutes = '0' + date.getMinutes();
  }else{
    minutes = date.getMinutes();
  }
  return date.getHours() + ':' + minutes;
});

/**
 * 
 */
Vue.filter('fmtDate2', (time) => {
  let oldDate = new Date(time)
  let newDate = new Date()
  var dayNum = "";
  var getTime = (newDate.getTime() - oldDate.getTime())/1000;

  if(getTime < 60*5){
    dayNum = "";
  }else if(getTime >= 60*5 && getTime < 60*60){
    dayNum = parseInt(getTime / 60) + "";
  }else if(getTime >= 3600 && getTime < 3600*24){
    dayNum = parseInt(getTime / 3600) + "";
  }else if(getTime >= 3600 * 24 && getTime < 3600 * 24 * 30){
    dayNum = parseInt(getTime / 3600 / 24 ) + "";
  }else if(getTime >= 3600 * 24 * 30 && getTime < 3600 * 24 * 30 * 12){
    dayNum = parseInt(getTime / 3600 / 24 / 30 ) + "";
  }else if(time >= 3600 * 24 * 30 * 12){
    dayNum = parseInt(getTime / 3600 / 24 / 30 / 12 ) + "";
  }

  // 1,0
  function addZero(val){
    if(val < 10){
      val = '0' + String(val);
    }
    return val;
  }

  let year   = oldDate.getFullYear();
  let month  = oldDate.getMonth()+1;
  let day    = oldDate.getDate();
  let hour   = oldDate.getHours();
  let minute = oldDate.getMinutes();
  let second = oldDate.getSeconds();

  month = addZero(month);
  day = addZero(day);
  hour = addZero(hour);
  minute = addZero(minute);
  second = addZero(second);

  return dayNum+" "+year+"-"+month+"-"+day+" "+hour+":"+minute+":"+second;
});



