export default {
  name:'Login',
  data(){
    return{
      userNumber:'',
      userPassword:''
    }
  },
  mounted () {
    // tab
    this.$store.dispatch('hideTabBar');
  },
  methods:{
    /**/
    findPassword() {
      this.$toast.center('');
    },
    /**/
    register() {
      this.$toast.center('');
    },
    /**/
    login() {
      if(!this.userNumber){
        this.$toast.center('');
        return;
      }else if(!this.userPassword){
        this.$toast.center('');
        return;
      }
      /**/
      this.$toast.center('!');
      // home
      this.$router.push({path:'/'});
    }
  }
}



