/**
 * 
 * vuex 
 */
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
import * as actions from './actions'
import * as mutations from './mutations'

const store = new Vuex.Store({
  actions,
  mutations,

  state: {
    tabbar:true // tab
  },

  getters: {
    tabbarShow (state) {
      return state.tabbar;
    }
  }
})

export default store;



